import React, { useState } from 'react'
import styled from 'styled-components'
import { Navbar, Modal, Button } from 'react-bootstrap'
import { Link } from 'gatsby'

const StyledHeader = styled.div`
    grid-area: header;
`

export default function Header() {
    const [price, setPrice] = useState(false);

    return <StyledHeader>
        <Navbar bg="dark" variant="dark"  >
            <Navbar.Brand as={Link} to="/"></Navbar.Brand>
            <Navbar.Brand as={Link} to="/tutorial/quick-started"></Navbar.Brand>
            <Navbar.Brand as={Link} to="/tutorial/document-list"></Navbar.Brand>
            <Navbar.Brand style={{cursor: 'pointer'}} onClick={() => setPrice(true)} ></Navbar.Brand>
        </Navbar>

        <Modal show={price} onHide={() => setPrice(false)}>
            <Modal.Header closeButton>
            <Modal.Title></Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div style={{color: 'red'}} ></div>
                
                ,
                
            </Modal.Body>
            <Modal.Footer>
            <Button variant="secondary" onClick={() => setPrice(false)}>
                
            </Button>
            </Modal.Footer>
        </Modal>
    </StyledHeader>
}
